import React from "react";
import './index.css'
import api from "../../../api/api";
import { message, Icon, Modal, Input, Collapse } from "antd";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { isAuthenticated } from "../../../utils/Session";
const Search = Input.Search
const Panel = Collapse.Panel;
const getSvgIcon = (style = {}, text) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    const path1 = 'M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z'
    return (
        <i style={style}>
            <svg
                viewBox="0 0 1024 1024"
                width="1em"
                height="1em"
                fill="currentColor"
                style={{ verticalAlign: '-.125em' }}
            >
                <path d={path1} />
            </svg>
        </i>
    );
};


function expandIcon(props) {
    return getSvgIcon({
        position: 'absolute',
        right: 'auto',
        left: '20px',
        color: '#B4B4B4',
        transform: `rotate(${props.isActive ? 0 : -90}deg)`,
        top: 0
    }, props);
}
class Index extends React.Component {
    state = {
        status: 1,
        list: [],
        pageNo: 1,
        listCategory: [],
        keys: ['0'],
        artcileList: [],
        visibleModal: false,
        deptIdCheck: 0,
        articleId:'',
        visibleBanner:false,
        info:''
    }
    setStatus = (val) => {
        this.setState({
            status: val
        })
        NProgress.start()
        api('listBanner', 'post', {}, { status: val }).then(res => {
            NProgress.done()
            if (!res.data.errorMessage) {

                this.setState({
                    list: res.data.data,

                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })

    }
    componentWillMount() {
        console.log(isAuthenticated()   )
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId
        })
        api('listBanner', 'post', {}, { status: 1 }).then(res => {
            if (!res.data.errorMessage) {

                this.setState({
                    list: res.data.data,

                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })
        api('listCategory', 'post', {}, { 'type': 1, status: 1 }).then(res => {
            if (!res.data.errorMessage) {

                this.setState({
                    listCategory: res.data.data,

                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    visibleModalOk = () => {

        this.setState({
            visibleModal: true
        })
    }
    visibleModalCancel = () => {
        this.setState({
            visibleModal: false
        })
    }
    visibleModalOkBanner = () => {

        this.setState({
            visibleBanner: true
        })
    }
    visibleModalCancelBanner = () => {
        this.setState({
            visibleBanner: false
        })
    }
    setVisble = (val) => {
        api('editBannerStatus', 'post', {}, {
            'bannerId': val.id,
            'status': this.state.status == 1 ? 2 : 1,
            'adminId': this.state.backUserId
        }).then(res => {
            if (!res.data.errorMessage) {
                message.success('操作成功')

                api('listBanner', 'post', {}, { status: this.state.status }).then(res => {
                    if (!res.data.errorMessage) {

                        this.setState({
                            list: res.data.data,

                        })

                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })

            } else {
                message.warning(res.data.errorMessage)
            }

        })
    }
    onCollapseChange = (key) => {
        console.log(key)

        if (key) {
            api('getArticleListByCate', 'post',{}, {
                "categoryId": Number(key),
                "pageNo": 1,
                "pageSize": 10000,
                'status': 1,
                'adminId': this.state.backUserId
            }).then(res => {
                if (!res.data.errorMessage) {
                    this.setState({
                        artcileList: res.data.list
                    })
                }
            })
        }
    }
    onChangeSecond = (val) => {
        console.log(val)
        this.setState({
            deptIdCheck: val
        })
        api('getArticleListByCate', 'post',{}, {
            "categoryId": Number(val),
            "pageNo": 1,
            "pageSize": 10000,
            'status': 1,
            'adminId': this.state.backUserId
        }).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    artcileList: res.data.list
                })
            }
        })
    }
    onChangeArticle=(val)=>{
            this.setState({
                articleId:val.id==this.state.articleId?'':val.id,
                info:val.id==this.state.articleId?'':val
            })
        
    }
    sendAdjust=()=>{
       if(this.state.info){
        this.visibleModalOkBanner()
        this.visibleModalCancel()
       }else{
           message.warning('请选择文章！')
       }
    }
    saveBanner=()=>{
        api('saveBanner','post',{},{
            'imgurl':this.state.info.firstImg,
            'adminId':this.state.backUserId,
            'articleId': this.state.articleId,
            'jumpUrl':''
        }).then(res=>{
            if (!res.data.errorMessage) {
                this.visibleModalCancelBanner()
                message.success('添加成功')
                api('listBanner', 'post', {}, { status: this.state.status }).then(res => {
                    if (!res.data.errorMessage) {

                        this.setState({
                            list: res.data.data,

                        })

                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    render() {
        return (
            <div id="banner">
                <div className="bannerEditBox">
                    <span className={this.state.status == 1 ? 'status-active' : ''} onClick={() => this.setStatus(1)}>上架中</span>
                    <span className={this.state.status == 2 ? 'status-active' : ''} onClick={() => this.setStatus(2)}>下架中</span>
                    <span className='add' onClick={() => { this.visibleModalOk() }}>+ 添加轮播文章</span>
                </div>
                <div className="list">
                    <div className="list_head flexBox">
                        <div className="num">
                            序号
                        </div>
                        <div className="name">
                            标题
                        </div>
                        <div className="status">
                            状态
                        </div>
                        <div className="operation flex">
                            操作
                        </div>
                    </div>
                    {
                        this.state.list.map((res, index) => {
                            if (index >= (this.state.pageNo - 1) * 10 && index < (this.state.pageNo) * 10) {
                                return (
                                    <div className="list_info flexBox" key={res.id}>
                                        <div className="num">
                                            {index <= 9 ? index + 1 == 10 ? index + 1 : '0' + (index + 1) : index + 1}
                                        </div>
                                        <div className="name">
                                            {res.title}
                                        </div>
                                        <div className="status">
                                            {res.status == 1 ? '已开启' : '已关闭'}
                                        </div>
                                        <div className="operation flex">
                                            <div onClick={() => {
                                                this.setVisble(res)
                                            }}> {res.status == 1 ? '关闭' : '开启'}</div>

                                        </div>
                                    </div>
                                )
                            }
                        })
                    }
                    {
                            this.state.list.length==0?<div className='noData'>
                                    <img src={require('../../../assets/images/img-nodata.png')} alt=""/>
                                    <p>暂无相关信息</p>
                                </div>:''
                        }
                    {
                        this.state.list.length <= 10 ? '' : <div className='pagination'>
                            <span onClick={this.setPrev}>
                                <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                            <span>
                                {this.state.pageNo}/{Math.ceil(this.state.list.length / 10)}
                            </span>
                            <span onClick={this.setNext}>
                                <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                        </div>
                    }
                </div>
                <Modal
                    title=""
                    visible={this.state.visibleModal}
                    onOk={this.visibleModalOk}
                    onCancel={this.visibleModalCancel}
                    footer={null}
                    closable={false}
                    centered={true}
                    width='51.56%'
                    destroyOnClose={true}
                    wrapClassName='addArticle'
                >
                    <div className="container">
                        <div className="flex select">
                            <div className="title">选择：</div>
                            <div className="content">
                                <div className="search">
                                    <Search
                                        placeholder="搜索"
                                        onSearch={this.getSearch}
                                        style={{ width: '91.63%' }}
                                    />
                                </div>
                                <div className="deptList">
                                    <div className="firstDrpt">
                                        <div className="head" >
                                            <Icon type="caret-down" style={{ 'color': '#B4B4B4' }} /><span>最近文章</span>
                                        </div>
                                        <Collapse accordion expandIcon={expandIcon} bordered={false} showArrow={false} onChange={this.onCollapseChange}>
                                            {
                                                this.state.listCategory && this.state.listCategory.map((res, index) => {
                                                    return (
                                                        <Panel header={<span onClick={() => { this.onChangeSecond(res.id) }}>

                                                            <span>{res.name}</span>
                                                        </span>
                                                        } key={res.id} >
                                                            <div >
                                                                {res.list && res.list.map((res, i) => {
                                                                    if (res && res.name) {
                                                                        return (<span style={this.state.deptIdCheck == res.id ? { 'background': '#F1948E', 'color': '#fff' } : { 'background': '#f4f6f8' }} className='second' onClick={() => {
                                                                            this.onChangeSecond(res.id)
                                                                        }} key={res.id}>{res.title}</span>)
                                                                    }


                                                                })}
                                                            </div>
                                                        </Panel>
                                                    )
                                                })
                                            }

                                        </Collapse>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div className="flex selected">
                            <div className="title">已选：</div>
                            <div className="content">
                                {this.state.artcileList && this.state.artcileList.map((res, i) => {
                                    if (res.firstImg ) {
                                        return (<span className='second' onClick={() => {
                                            this.onChangeArticle(res)
                                        }}>{
                                                this.state.articleId == res.id ? <Icon type="check-circle" theme="filled" /> : <span className='icon'></span>
                                            }{res.title}</span>)
                                    }


                                })}
                            </div>

                        </div>

                    </div>
                    <div className="btn">
                        <div className="send" onClick={this.sendAdjust}>确认</div>
                        <div className="close" onClick={this.visibleModalCancel}>取消</div>
                    </div>
                </Modal>
                <Modal
                    title="添加文章"
                    visible={this.state.visibleBanner}
                    onOk={this.visibleModalOkBanner}
                    onCancel={this.visibleModalCancelBanner}
                    footer={null}
                    closable={false}
                    centered={true}
                    width='51.56%'
                    destroyOnClose={true}
                    wrapClassName='addBanner'
                >
                    <div className="content">
                        <img src={this.state.info.firstImg} alt=""/>
                        <div className="title">{this.state.info.title}</div>
                       
                    </div>
                    <div className="tips">
                        轮播文章必须带有图片
                        </div>
                     <div className="btn">
                        <div className="send" onClick={this.saveBanner}>确认</div>
                        <div className="close" onClick={this.visibleModalCancelBanner}>取消</div>
                    </div>
                </Modal>

            </div>
        )
    }
}
export default Index